<template>
  <div>
    <div class="adiv">
      <van-icon name="arrow-left" class="icon" @click="$router.back()" />
      <h1>忘记密码</h1>
    </div>
     <van-form @submit="onSubmit1" >
      <van-field
        v-model="password"
        type="password"
        name="oldPassword"
        label="旧密码"
        placeholder="旧密码"
      />

      <van-field
        v-model="password1"
        type="password"
        name="newPassword"
        label="新密码"
        placeholder="新密码"
      />
    
      
      <van-button
        block
        type="info"
        native-type="submit"
        :color="myColor"
        class="btn"
        >提交</van-button
      >
     
    </van-form>
  </div>
</template>

<script>
import {getupdateAPI} from "@/api/home"
export default {
    name:"modify",
    data() {
        return {
           password: "",
      password1: "",
         loginfo: JSON.parse(sessionStorage.getItem("loginfo")),

        }
    },
    methods: {
       
    onSubmit1(values) {
   
      getupdateAPI({
        oldPassword:this.password,newPassword:this.password1,uid:this.loginfo.uid
      }).then((result) => {
  
          this.$toast(result.data.msg);
      this.$notify({ type: "danger", message:result.data.msg });
      if(result.data.msg=="修改成功"){
          this.$router.push('/login')
      }
      }).catch((err) => {
        console.log('err: ', err);
        
      });
    },
    },

}
</script>

<style lang="scss" scoped>
.adiv {
  width: 100vw;
  height: 50px;
  background-color: $mycolor;
  display: flex;
  align-items: center;

  color: aliceblue;
  font-size: 14px;
  margin-bottom: 50px;
}
h1 {
  margin: 0 auto;
  font-size: 16px;
}
.icon {
  font-size: 32px;
}
.yzm {
  float: right;
  margin: 20px;
}
</style>

